.reason {
  position: relative;
  padding: 27px 0 28px;
  background-color: #eeeeee;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 20px;
    display: block;
    width: 280px;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.1);
  }

  &--accent {
    padding: 39px 0;
    padding-bottom: 0;
    background-color: #81b3d2;

    &::after {
      display: none;
    }
  }

  &:last-child {
    padding-bottom: 27px;

    &::after {
      display: none;
    }
  }
}

.reason__description {
  padding: 0 35px;
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
  text-align: center;
  color: #333333;

  &--accent {
    padding: 0 20px;
    color: #ffffff;
  }
}

.reason__title {
  margin: 0;
  padding: 0;
  margin-bottom: 9px;
  color: #000000;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;

  &--accent {
    color: #ffffff;
  }
}

.reason__number {
  display: block;
  margin-bottom: 15px;
}

.reason__text {
  margin: 0;
  padding: 0;

  &--accent {
    margin-bottom: 39px;
  }

  &--city {
    margin-bottom: 40px;
  }
}

.reason__image {
  width: 320px;
  height: 260px;

  &--city {
    background-image: url("../img/main-city-mobile@1x.jpg");
    background-repeat: no-repeat;
  }

  &--bridge {
    background-image: url("../img/main-bridge-mobile@1x.jpg");
    background-repeat: no-repeat;
  }

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    &--city {
      background-image: url("../img/main-city-mobile@2x.jpg");
      background-size: 320px 260px;
    }

    &--bridge {
      background-image: url("../img/main-bridge-mobile@2x.jpg");
      background-size: 320px 260px;
    }
  }
}

@media (min-width: @tablet-width) {
  .reason {
    display: flex;
    padding: 0;

    &:last-child {
      padding-bottom: 0;
    }

    &--reverse {
      flex-direction: row-reverse;
    }

    &--accent {
      width: 100%;
      padding: 0;
    }

    &::after {
      display: none;
    }
  }

  .reason__description {
    width: 256px;
    padding: 45px 49px 44px;
    box-sizing: border-box;

    &--accent {
      padding: 45px 43px 0;
    }
  }

  .reason__title {
    font-size: 21px;
  }

  .reason__number {
    margin-bottom: 22px;
  }

  .reason__image {
    width: 512px;
    height: 256px;

    &--city {
      background-image: url("../img/main-city-tablet@1x.jpg");
    }

    &--bridge {
      background-image: url("../img/main-bridge-tablet@1x.jpg");
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      &--city {
        background-image: url("../img/main-city-tablet@2x.jpg");
        background-size: 512px 256px;
      }

      &--bridge {
        background-image: url("../img/main-bridge-tablet@2x.jpg");
        background-size: 512px 256px;
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .reason__description {
    width: 400px;
    padding-bottom: 65px;

    &--accent {
      padding-bottom: 0;
    }
  }

  .reason__title {
    padding: 0 75px;

    &--accent {
      padding: 8px 49px;
      margin-bottom: 1px;
    }
  }

  .reason__text {
    &--city {
      padding: 0 25px;
    }

    &--bridge {
      padding: 0 40px;
    }
  }

  .reason__image {
    width: 800px;

    &--city {
      background-image: url("../img/main-city-desktop@1x.jpg");
      background-position: top -71px center;
    }

    &--bridge {
      background-image: url("../img/main-bridge-desktop@1x.jpg");
      background-position: top -76px center;
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      &--city {
        background-image: url("../img/main-city-desktop@2x.jpg");
        background-size: 800px auto;
      }

      &--bridge {
        background-image: url("../img/main-bridge-desktop@2x.jpg");
        background-size: 800px auto;
      }
    }
  }
}
