.emotions {
  display: flex;
  flex-direction: column;
  padding: 18px 20px 51px;
  background-color: #ffffff;
}

.emotions__title {
  position: relative;
  padding: 0;
  margin: 0;
  margin-bottom: 17px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
}

.emotions__textarea {
  width: 279px;
  min-height: 160px;
  padding: 16px 21px;
  font-size: 18px;
  font-weight: bold;
  color: #000000;
  line-height: 26px;
  background-color: #f2f2f2;
  border: none;
  resize: none;
  box-sizing: border-box;

  &:hover {
    background-color: #e5e5e5;
  }

  &:active {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 5px #e5e5e5;
  }

  &:focus {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 5px #e5e5e5;
  }

  &::placeholder {
    font-size: 18px;
    font-weight: bold;
    color: #000000;
    line-height: 26px;
    opacity: 1;
  }
}

@media (min-width: @tablet-width) {
  .emotions {
    width: 100%;
    padding: 0;
    border: none;
    box-sizing: border-box;
    margin-bottom: 58px;
  }

  .emotions__title {
    font-size: 21px;
    margin-bottom: 57px;

    &::before {
      content: "";
      position: absolute;
      top: 12px;
      left: 0;
      width: 124px;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
    }

    &::after {
      content: "";
      position: absolute;
      top: 12px;
      right: 0;
      width: 124px;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }

  .emotions__textarea {
    width: 100%;
  }
}

@media (min-width: @desktop-width) {
  .emotions__title {
    &::before {
      width: 280px;
    }

    &::after {
      width: 280px;
    }
  }
}
