.content-header {
  position: relative;
  width: 320px;
  margin: 0 auto;
  padding: 40px 30px 43px;
  text-align: center;
  background-color: #ffffff;
  box-sizing: border-box;

  &--photo {
    padding: 36px 25px 27px;
  }

  &--form {
    padding: 36px 20px 17px;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
  }

  &--index {
    &::before {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 320px;
      height: 17px;
      border-width: 15px 160px 0;
      border-style: solid;
      border-color: #81b3d2;
      border-top-color: transparent;
      box-sizing: border-box;
    }
  }
}

.content-header__title {
  padding: 0;
  margin: 0;
  margin-bottom: 26px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;

  &--index {
    margin-bottom: 27px;
  }
}

.content-header__dash {
  margin: 0 3px;
}

.content-header__intro {
  padding: 0;
  margin: 0;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #333333;

  &--index {
    line-height: 26px;
  }
}

@media (min-width: @tablet-width) {
  .content-header {
    width: 768px;
    padding: 75px 165px 74px;

    &--index {
      padding: 50px 71px;

      &::before {
        display: none;
      }
    }

    &--photo {
      padding-bottom: 69px;
      box-shadow: none;
    }

    &--form {
      box-shadow: none;
    }
  }

  .content-header__title {
    font-size: 30px;
    margin-bottom: 30px;

    &--index {
      font-size: 21px;
      margin-bottom: 28px;
    }
  }
}

@media (min-width: @desktop-width) {
  .content-header {
    width: 1200px;
    padding: 111px 380px;
    padding-bottom: 59px;

    &--index {
      padding: 50px 350px;
    }

    &--photo {
      padding-bottom: 71px;
    }
  }
}
