.map {
  min-width: 320px;
  margin: 0 auto;
}

.map__container {
  position: relative;

  &::before {
    display: block;
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 27px;
    height: 27px;
    transform: translate(-50%, -50%);
    background-image: url("../img/icon-map-marker.svg");
    background-repeat: no-repeat;
  }
}

.map__image {
  display: block;
  width: 320px;
  height: 350px;
}

.map__iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 350px;
  border: 0;
}

@media (min-width: @tablet-width) {
  .map__image {
    width: 768px;
  }

  .map__container {
    &::before {
      transform: translate(-14px, -11px);
    }
  }
}

@media (min-width: @desktop-width) {
  .map__image {
    width: 1200px;
  }

  .map__container {
    &::before {
      transform: translate(-50%, -50%);
    }
  }
}
