.photos {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}

.photos__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (min-width: @tablet-width) {
  .photos {
    width: 768px;
  }

  .photos__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 60px;
  }
}

@media (min-width: @desktop-width) {
  .photos {
    width: 1200px;
  }

  .photos__list {
    padding: 0;
  }
}
