.contacts {
  padding: 18px 20px 27px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.contacts__title {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
}

.contacts__item {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 11px;

  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 58px;
    height: 58px;
    background-color: #e6e6e6;
    background-repeat: no-repeat;
    background-position: center;
  }

  &--phone {
    &::before {
      background-image: url("../img/icon-phone.svg");
    }
  }

  &--email {
    &::before {
      background-image: url("../img/icon-mail.svg");
    }
  }
}

.contacts__label {
  margin-left: 22px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: normal;
  line-height: 26px;
  color: #333333;
}

.contacts__input {
  width: 100%;
  padding: 15px 21px 17px;
  font-size: 18px;
  font-weight: bold;
  color: #000000;
  line-height: 26px;
  background-color: #f2f2f2;
  border: none;
  box-sizing: border-box;

  &:hover {
    background-color: #e5e5e5;
  }

  &:active {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 5px #e5e5e5;
  }

  &:focus {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 5px #e5e5e5;
  }

  &::placeholder {
    font-size: 18px;
    font-weight: bold;
    color: #000000;
    line-height: 26px;
    opacity: 1;
  }

  &--invalid {
    background-color: red;
  }

  &:invalid {
    background-color: red;
  }
}

@media (min-width: @tablet-width) {
  .contacts {
    width: 100%;
    padding: 0;
    padding-top: 69px;
    border: none;
    box-sizing: border-box;
  }

  .contacts__title {
    position: relative;
    font-size: 21px;
    margin-bottom: 24px;

    &::before {
      content: "";
      position: absolute;
      top: 12px;
      left: 0;
      width: 124px;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
    }

    &::after {
      content: "";
      position: absolute;
      top: 12px;
      right: 0;
      width: 124px;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }

  .contacts__item {
    margin-bottom: 22px;
  }
}

@media (min-width: @desktop-width) {
  .contacts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 67px;
  }

  .contacts__title {
    width: 100%;
    margin-bottom: 75px;

    &::before {
      width: 280px;
    }

    &::after {
      width: 280px;
    }
  }

  .contacts__item {
    flex-direction: row;
    align-items: center;
    width: 50%;
    margin: 0;
    box-sizing: border-box;

    &--phone {
      width: 401px;
    }
  }

  .contacts__label {
    margin: 0;
    margin-right: auto;
  }

  .contacts__text-hidden {
    display: none;
  }

  .contacts__input {
    width: 315px;
  }
}
