.impression {
  padding: 18px 0 26px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.impression__title {
  padding: 0;
  margin: 0;
  margin-bottom: 22px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
}

.impression__item {
  position: relative;
  padding: 15px 0 17px 99px;
  margin-bottom: 11px;

  &:last-child {
    margin-bottom: 0;
  }
}

.impression__label {
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-transform: none;

  &::before {
    content: "";
    position: absolute;
    top: 1px;
    left: 20px;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background-color: #f2f2f2;
  }

  &::after {
    content: none;
    position: absolute;
    top: 18px;
    left: 37px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: #333333;
  }

  &:hover {
    &::before {
      background-color: #e5e5e5;
    }
  }

  &:active {
    &::before {
      background-color: #d5d5d5;
    }
  }
}

.impression__input {
  position: absolute;
  appearance: none;
}

.impression__input:checked + .impression__label {
  &::after {
    content: "";
  }

  &:active {
    &::after {
      opacity: 0.3;
    }
  }
}

.impression__input:focus + .impression__label {
  &::before {
    outline: 2px solid #73b3da;
  }
}

@media (min-width: @tablet-width) {
  .impression {
    width: 50%;
    padding: 0;
    order: -1;
    border: none;
    box-sizing: border-box;
  }

  .impression__title {
    font-size: 21px;
    text-align: left;
    margin-bottom: 58px;
  }

  .impression__item {
    padding-left: 75px;
    margin-bottom: 20px;
  }

  .impression__label {
    &::before {
      left: 0;
    }

    &::after {
      left: 17px;
    }
  }
}

@media (min-width: @desktop-width) {
  .impression__title {
    padding-left: 80px;
  }

  .impression__item {
    padding-left: 80px;
  }

  .impression__label {
    &::before {
      left: 4px;
    }

    &::after {
      left: 21px;
    }
  }
}
