.forms {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}

.forms__footer {
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  padding-bottom: 37px;
  background-color: #ffffff;
}

.forms__submit {
  padding: 16px 0;
  font-family: inherit;
  font-size: 21px;
  font-weight: bold;
  line-height: 26px;
  text-transform: uppercase;
  border: none;
}

.forms__note {
  display: none;
  padding: 0;
  margin: 0;
  font-size: 14px;
  font-weight: normal;
  line-height: 26px;
}

@media (min-width: @tablet-width) {
  .forms {
    display: flex;
    flex-wrap: wrap;
    width: 768px;
    padding: 0 60px;
    padding-bottom: 61px;
    box-sizing: border-box;
  }

  .forms__footer {
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    margin: 0;
  }

  .forms__submit {
    width: 324px;
    padding-right: 11px;
  }

  .forms__note {
    display: block;
    padding-top: 18px;
  }
}

@media (min-width: @desktop-width) {
  .forms {
    width: 1200px;
    padding: 0 120px;
    padding-bottom: 66px;
  }

  .forms__submit {
    width: 480px;
    padding-right: 0;
  }
}
