.landmarks {
  padding: 18px 0 23px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.landmarks__title {
  padding: 0;
  margin: 0;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
}

.landmarks__item {
  position: relative;
  padding: 15px 0 18px 99px;
  margin-bottom: 21px;

  &:last-child {
    margin-bottom: 0;
  }
}

.landmarks__label {
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-transform: none;

  &::before {
    content: "";
    position: absolute;
    top: 1px;
    left: 19px;
    width: 56px;
    height: 56px;
    border-radius: 6px;
    background-color: #f2f2f2;
  }

  &::after {
    content: none;
    position: absolute;
    top: 18px;
    left: 37px;
    width: 17px;
    height: 11px;
    border-left: 4px solid #333333;
    border-bottom: 4px solid #333333;
    transform: rotate(-45deg);
  }

  &:hover {
    &::before {
      background-color: #e5e5e5;
    }
  }

  &:active {
    &::before {
      background-color: #d5d5d5;
    }
  }
}

.landmarks__input {
  position: absolute;
  appearance: none;
  opacity: 0;
}

.landmarks__input:checked + .landmarks__label {
  &::after {
    content: "";
  }

  &:active {
    &::after {
      opacity: 0.3;
    }
  }
}

.landmarks__input:focus + .landmarks__label {
  &::before {
    outline: 2px solid #73b3da;
  }
}

@media (min-width: @tablet-width) {
  .landmarks {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding: 0;
    padding-top: 52px;
    margin-bottom: 36px;
    border: none;
    box-sizing: border-box;
  }

  .landmarks__title {
    position: relative;
    padding: 0 117px;
    font-size: 21px;
    margin-bottom: 42px;

    &::before {
      content: "";
      position: absolute;
      top: 25px;
      left: 0;
      width: 124px;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
    }

    &::after {
      content: "";
      position: absolute;
      top: 25px;
      right: 0;
      width: 124px;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }

  .landmarks__item {
    width: 324px;
    padding: 14px 0 17px 110px;
    margin-bottom: 31px;
    box-sizing: border-box;

    &:nth-child(even) {
      padding-left: 175px;

      & .landmarks__label {
        &::before {
          left: 95px;
        }

        &::after {
          left: 113px;
        }
      }
    }
  }

  .landmarks__label {
    &::before {
      top: 0;
      left: 30px;
    }

    &::after {
      left: 48px;
      height: 10px;
    }
  }
}

@media (min-width: @desktop-width) {
  .landmarks {
    margin-bottom: 65px;
  }

  .landmarks__title {
    width: 100%;
    padding: 0 280px;

    &::before {
      width: 280px;
    }

    &::after {
      width: 280px;
    }
  }

  .landmarks__item {
    width: auto;
    margin: 0;
    margin-right: 70px;

    &:nth-child(n) {
      padding-left: 80px;

      & .landmarks__label {
        &::before {
          left: 0;
        }

        &::after {
          left: 17px;
        }
      }
    }

    &:last-child {
      margin-right: 3px;
    }

    &:nth-child(4) {
      margin-right: auto;
    }
  }
}
