.popup-fail {
  position: fixed;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%);
  width: 320px;
  padding-top: 39px;
  background-color: #ffffff;
  box-shadow: 0 6px 12px -2px rgba(0, 0, 0, 0.2);
  z-index: 1;

  &--hidden {
    display: none;
  }
}

.popup-fail__header {
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  color: #000000;
  text-align: center;
}

.popup-fail__description {
  padding: 0 45px;
  margin: 0;
  margin-bottom: 25px;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #333333;
  text-align: center;
}

.popup-fail__button-wrapper {
  padding: 0 70px 35px;
  box-sizing: border-box;
}

.popup-fail__button {
  width: 100%;
  padding: 16px 0;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  border: none;
}

@media (min-width: @tablet-width) {
  .popup-fail {
    width: 400px;
    padding-top: 57px;
  }

  .popup-fail__header {
    padding: 0 75px;
    font-size: 30px;
    line-height: 40px;
  }

  .popup-fail__description {
    padding: 0 45px;
    margin-bottom: 30px;
  }

  .popup-fail__button-wrapper {
    padding: 0 64px 49px;
  }

  .popup-fail__button {
    font-size: 21px;
  }
}
