.site-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.site-list__item {
  background-color: #81b3d2;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.2);

  &:first-child {
    box-shadow: none;
  }

  &:last-child {
    border-bottom: none;
    box-shadow:
      inset 0 -2px 8px -5px rgba(1, 1, 1, 0.8),
      inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
  }

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
  }
}

.site-list__link {
  display: block;
  padding: 8px 0 7px;
  text-align: center;
  font-size: 18px;
  line-height: 40px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    background-color: #ffffff;
  }

  .site-list__item {
    width: 151px;
    background-color: #ffffff;
    border-bottom: none;
    box-shadow: none;

    &:first-child {
      width: 156px;
    }

    &:nth-child(2) {
      margin-right: auto;
    }

    &:last-child {
      box-shadow: none;
    }

    &:hover {
      background-color: #ffffff;
    }

    &:active {
      background-color: #ffffff;
    }
  }

  .site-list__link {
    padding: 15px 0;
    text-align: center;
    font-size: 14px;
    line-height: 26px;
    font-weight: bold;
    color: #000000;

    &:hover {
      color: #81b3d2;
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);
    }

    &--active {
      color: #766357;

      &:hover {
        color: #604e43;
      }

      &:active {
        color: rgba(118, 99, 87, 0.3);
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .site-list__item {
    width: 239px;

    &:first-child {
      width: 239px;
    }
  }
}
