.copyright {
  position: relative;
  padding-right: 40px;
}

.copyright__text {
  font-size: 14px;
  font-weight: normal;
  line-height: 26px;
}

.copyright__link:focus .copyright__logo {
  outline: 1px solid lightblue;
}

.copyright__logo {
  position: absolute;
  top: -3px;
  right: 0;
  width: 27px;
  height: 34px;

  &:hover {
    fill: #81b3d2;
  }

  &:active {
    fill: rgba(0, 0, 0, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .copyright {
    padding-top: 10px;
  }

  .copyright__logo {
    top: 7px;
  }
}
