.presentation {
  width: 320px;
  margin: 0 auto;
  padding-bottom: 39px;
  background-color: #ffffff;
}

.presentation__header {
  padding: 34px 25px 0;
  margin-bottom: 35px;
  text-align: center;
}

.presentation__title {
  padding: 0;
  margin: 0;
  margin-bottom: 26px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
}

.presentation__intro {
  padding: 0;
  margin: 0;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #333333;
}

.presentation__image {
  display: block;
}

.presentation__options {
  position: relative;
  padding: 0 20px;
  margin-top: -45px;
  z-index: 1;
}

.presentation__controls {
  height: 111px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6) 45px, #f2f2f2 45px);
}

.presentation__roller {
  position: absolute;
  top: 22px;
  left: 42px;
  width: 235px;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.2);
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: -3px;
    left: 36px;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: #ffffff;
  }
}

.presentation__replay {
  position: absolute;
  top: 67px;
  left: 45px;
  cursor: pointer;
}

.presentation__replay-icon {
  width: 16px;
  height: 20px;
  fill: #5c6870;

  &:hover {
    fill: #333333;
  }

  &:active {
    fill: rgba(51, 51, 51, 0.3);
  }
}

.presentation__subtitles {
  position: absolute;
  top: 68px;
  right: 94px;
  cursor: pointer;
}

.presentation__subtitles-icon {
  width: 32px;
  height: 20px;
  fill: #5c6870;

  &:hover {
    fill: #333333;
  }

  &:active {
    fill: rgba(51, 51, 51, 0.3);
  }
}

.presentation__fullscreen {
  position: absolute;
  top: 68px;
  right: 43px;
  cursor: pointer;
}

.presentation__fullscreen-icon {
  width: 20px;
  height: 20px;
  fill: #5c6870;

  &:hover {
    fill: #333333;
  }

  &:active {
    fill: rgba(51, 51, 51, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .presentation {
    width: 768px;
    padding: 0 60px;
    padding-bottom: 40px;
    box-sizing: border-box;
  }

  .presentation__header {
    padding: 67px 110px 0;
    margin-bottom: 73px;
  }

  .presentation__title {
    font-size: 30px;
    margin-bottom: 30px;
  }

  //временно для сетки
  .presentation__image {
    width: 648px;
    height: 499px;
  }

  .presentation__options {
    padding: 0;
    margin-top: -92px;
  }

  .presentation__controls {
    height: 93px;
    background-image: none;
    background-color: rgba(0, 0, 0, 0.6);
  }

  .presentation__roller {
    top: 45px;
    left: 93px;
    width: 389px;

    &::before {
      left: 113px;
    }
  }

  .presentation__replay {
    top: 35px;
    left: 35px;
  }

  .presentation__replay-icon {
    fill: #ffffff;

    &:hover {
      fill: rgba(255, 255, 255, 0.6);
    }

    &:active {
      fill: rgba(255, 255, 255, 0.3);
    }
  }

  .presentation__subtitles {
    top: 36px;
    right: 92px;
  }

  .presentation__subtitles-icon {
    fill: #ffffff;

    &:hover {
      fill: rgba(255, 255, 255, 0.6);
    }

    &:active {
      fill: rgba(255, 255, 255, 0.3);
    }
  }

  .presentation__fullscreen {
    top: 36px;
    right: 41px;
  }

  .presentation__fullscreen-icon {
    fill: #ffffff;

    &:hover {
      fill: rgba(255, 255, 255, 0.6);
    }

    &:active {
      fill: rgba(255, 255, 255, 0.3);
    }
  }
}

@media (min-width: @desktop-width) {
  .presentation {
    width: 1200px;
    padding: 0 200px;
    padding-bottom: 85px;
  }

  .presentation__header {
    padding: 71px 178px 0;
    margin-bottom: 71px;
  }

  //временно для сетки
  .presentation__image {
    width: 800px;
    height: 500px;
  }

  .presentation__options {
    margin-top: -93px;
  }


  .presentation__roller {
    top: 45px;
    left: 92px;
    width: 540px;
  }

  .presentation__replay {
    top: 35px;
    left: 34px;
  }

  .presentation__subtitles {
    top: 36px;
    right: 94px;
  }

  .presentation__fullscreen {
    top: 36px;
    right: 43px;
  }
}
