.advantage {
  position: relative;
  padding: 0 40px 0 70px;
  margin-bottom: 28px;

  &:last-child {
    margin-bottom: 0;
  }
}

.advantage__icon {
  position: absolute;
  fill: #81b3d2;

  &--house {
    top: -9px;
    left: 22px;
    width: 38px;
    height: 36px;
  }

  &--gift {
    top: -11px;
    left: 25px;
    width: 32px;
    height: 38px;
  }

  &--burger {
    top: -5px;
    left: 18px;
    width: 40px;
    height: 35px;
  }
}

.advantage__title {
  padding: 0;
  margin: 0;
  margin-bottom: 14px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
}

.advantage__text {
  padding: 0;
  margin: 0;
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
  color: #333333;
}

@media (min-width: @tablet-width) {
  .advantage {
    width: 256px;
    padding: 0 50px;
    margin-bottom: 0;
    box-sizing: border-box;

    &--gift {
      padding: 0 40px;
      order: 1;
    }
  }

  .advantage__icon {
    &--house {
      top: -99px;
      left: 90px;
      width: 75px;
      height: 72px;
    }

    &--gift {
      top: -102px;
      left: 95px;
      width: 64px;
      height: 75px;
    }

    &--burger {
      top: -96px;
      left: 89px;
      width: 77px;
      height: 68px;
    }
  }

  .advantage__title {
    margin-bottom: 21px;
    font-size: 21px;
  }
}

@media (min-width: @desktop-width) {
  .advantage {
    width: 400px;
    padding: 0 50px;

    &--gift {
      padding: 0 90px;
    }
  }

  .advantage__icon {
    &--house {
      left: 161px;
    }

    &--gift {
      left: 167px;
    }

    &--burger {
      left: 161px;
    }
  }
}
