.photo {
  padding: 23px 20px 0;
  box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.1);

  &:last-child {
    box-shadow:
      inset 0 -1px 0 0 rgba(0, 0, 0, 0.1),
      inset 0 1px 0 0 rgba(0, 0, 0, 0.1);
  }
}

.photo__image-container&:focus .photo__image {
  outline: 2px solid lightblue;
}

.photo__image {
  display: block;
  width: 280px;
  height: 280px;
}

.photo__options {
  display: flex;
  flex-direction: column;
  padding-top: 15px;
}

.photo__title {
  padding: 0;
  margin: 0;
  font-size: 14px;
  font-weight: bold;
  line-height: 24px;
}

.photo__autor {
  padding: 0;
  margin: 0;
  margin-bottom: 7px;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #333333;
  text-transform: none;
}

.photo__likes-box {
  display: flex;
  margin-bottom: 16px;
}

.photo__likes-icon {
  width: 22px;
  height: 20px;
  fill: #b2b2b2;
}

.photo__likes-count {
  padding-top: 2px;
  padding-left: 15px;
  font-size: 14px;
  font-weight: bold;
  line-height: 24px;
}

@media (min-width: @tablet-width) {
  .photo {
    width: 324px;
    height: 347px;
    padding: 0;
    box-shadow: none;

    &:first-child {
      width: 100%;
      height: 480px;
    }

    &:first-child .photo__image {
      width: 100%;
      height: 400px;
    }

    &:last-child {
      box-shadow: none;
    }
  }

  .photo__image {
    width: 324px;
    height: 267px;
  }

  .photo__options {
    flex-direction: row;
    align-items: center;
    padding-left: 21px;
    padding-bottom: 10px;
    background-color: #f2f2f2;

    &:nth-child(3) {
      background-color: #eaeaea;
    }
  }

  .photo__likes-box {
    min-width: 85px;
    margin-left: auto;
    margin-bottom: 5px;
  }

  .photo__likes-count {
    padding-top: 0;
    padding-left: 10px;
  }
}

@media (min-width: @desktop-width) {
  .photo {
    width: 400px;
    height: 267px;

    &:first-child {
      height: 400px;
    }
  }

  .photo__image {
    width: 400px;
    height: 267px;
  }

  .photo__options {
    position: relative;
    padding: 15px 0 10px 21px;
    margin-top: -80px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1;
  }

  .photo__autor {
    color: #ffffff;
  }

  .photo__likes-icon {
    fill: #ffffff;
  }
}
