.advantages-list {
  padding: 0;
  padding-top: 35px;
  margin: 0;
  padding-bottom: 37px;
  list-style: none;
  background-color: #ffffff;
}

@media (min-width: @tablet-width) {
  .advantages-list {
    display: flex;
    justify-content: space-between;
    padding-top: 159px;
    padding-bottom: 61px;
    text-align: center;
    box-sizing: border-box;
  }
}

@media (min-width: @desktop-width) {
  .advantages-list {
    padding-bottom: 82px;
  }
}
