.user-initials {
  padding: 19px 20px 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.user-initials__title {
  padding: 0;
  margin: 0;
  margin-bottom: 22px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
}

.user-initials__item {
  display: flex;
  align-items: baseline;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.user-initials__label {
  margin-right: auto;
  font-size: 14px;
  font-weight: normal;
  line-height: 26px;
  color: #333333;
}

.user-initials__input {
  padding: 15px 21px 17px;
  font-size: 18px;
  font-weight: bold;
  color: #000000;
  line-height: 26px;
  background-color: #f2f2f2;
  border: none;
  box-sizing: border-box;

  &:hover {
    background-color: #e5e5e5;
  }

  &:active {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 5px #e5e5e5;
  }

  &:focus {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 5px #e5e5e5;
  }

  &--name {
    width: 230px;
  }

  &--surname {
    width: 192px;
  }

  &--patronymic {
    width: 194px;
  }

  &::placeholder {
    font-size: 18px;
    font-weight: bold;
    color: #000000;
    line-height: 26px;
    opacity: 1;
  }

  &--invalid {
    background-color: red;
  }

  &:invalid {
    background-color: red;
  }
}

@media (min-width: @tablet-width) {
  .user-initials {
    position: relative;
    width: 50%;
    padding: 0;
    padding-left: 58px;
    border: none;
    box-sizing: border-box;

    &::before {
      content: "";
      position: absolute;
      top: 5px;
      left: -1px;
      width: 1px;
      height: 292px;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }

  .user-initials__title {
    font-size: 21px;
    text-align: left;
    margin-bottom: 58px;
  }

  .user-initials__input {
    &--name {
      width: 216px;
    }

    &--surname {
      width: 178px;
    }

    &--patronymic {
      width: 180px;
    }
  }
}

@media (min-width: @desktop-width) {
  .user-initials {
    padding-left: 80px;

    &::before {
      left: 0;
    }
  }

  .user-initials__input {
    &--name {
      width: 350px;
    }

    &--surname {
      width: 312px;
    }

    &--patronymic {
      width: 314px;
    }
  }
}
