.page-header {
  position: relative;
  width: 320px;
  margin: 0 auto;
}

.page-header__logo {
  position: relative;
  height: 56px;
  background-color: #ffffff;
}

.page-header__logo-link:focus .page-header__logo-image {
  outline: 1px solid lightblue;
}

.page-header__logo-image {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 102px;
  height: 84px;
  z-index: 1;
}

.page-header__background-image {
  position: relative;
  width: 100%;
  height: 160px;
  background-color: #3a4b54;
  background-image: url("../img/text-sedona.svg"), url("../img/bg-back-mobile@1x.jpg");
  background-size: 236px 40px, auto;
  background-position: top 51px left 42px, bottom 2px left -225px;
  background-repeat: no-repeat;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/text-sedona.svg"), url("../img/bg-back-mobile@2x.jpg");
    background-size: 236px 40px, 768px auto;
    background-position: top 51px left 42px, bottom 2px left -226px;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 320px;
    height: 29px;
    border-width: 26px 160px 0;
    border-style: solid;
    border-color: #ffffff;
    border-top-color: transparent;
    box-sizing: border-box;
  }
}

.page-header__background-image--index {
  width: 100%;
  height: 389px;
  background-color: #e1eef6;
  background-image: url("../img/text-sedona-index.svg"), url("../img/bg-back-mobile@1x.jpg");
  background-size: 282px 214px, auto;
  background-position: top 90px right 19px, center;
  background-repeat: no-repeat;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/text-sedona-index.svg"), url("../img/bg-back-mobile@2x.jpg");
    background-size: 282px 214px, 768px auto;
    background-position: top 90px right 19px, center right -223px;
  }

  &::before {
    height: 27px;
  }
}

@media (min-width: @tablet-width) {
  .page-header {
    position: relative;
    width: 768px;
  }

  .page-header__logo {
    position: static;
    height: 0;
  }

  .page-header__logo-image {
    width: 102px;
    height: 84px;
  }

  .page-header__background-image {
    height: 290px;
    background-image: url("../img/text-sedona.svg"), url("../img/bg-back-tablet@1x.jpg");
    background-size: 460px 80px, auto;
    background-position: top 94px right 153px, bottom center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/text-sedona.svg"), url("../img/bg-back-tablet@2x.jpg");
      background-size: 460px 80px, 768px auto;
      background-position: top 94px right 153px, bottom center;
    }

    &::before {
      width: 768px;
      height: 36px;
      bottom: 0;
      border-width: 36px 384px 0;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 34px;
      width: 700px;
      height: 57px;
      border-width: 57px 350px 0;
      border-style: solid;
      border-color: #ffffff;
      border-top-color: transparent;
      box-sizing: border-box;
    }
  }

  .page-header__background-image--index {
    height: 518px;
    background-image: url("../img/text-sedona-index.svg"), url("../img/bg-back-tablet@1x.jpg");
    background-size: 460px 350px, auto;
    background-position: top 77px right 154px, center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/text-sedona-index.svg"), url("../img/bg-back-tablet@2x.jpg");
      background-size: 460px 350px, 768px auto;
      background-position: top 77px right 154px, center;
    }
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    width: 1200px;
  }

  .page-header__logo-image {
    width: 186px;
    height: 96px;
  }

  .page-header__background-image {
    height: 302px;
    background-image: url("../img/text-sedona.svg"), url("../img/bg-back-desktop@1x.jpg");
    background-size: 460px 80px, auto;
    background-position: top 102px left 369px, bottom 1px center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/text-sedona.svg"), url("../img/bg-back-desktop@2x.jpg");
      background-size: 460px 80px, 1200px auto;
      background-position: top 102px left 369px, bottom 1px center;
    }

    &::before {
      width: 1200px;
      height: 58px;
      border-width: 57px 600px 0;
    }

    &::after {
      left: 250px;
      width: 700px;
      height: 58px;
      border-width: 57px 350px 0;
    }
  }

  .page-header__background-image--index {
    height: 518px;
    background-image: url("../img/text-sedona-index.svg"), url("../img/bg-back-desktop@1x.jpg");
    background-size: 460px 350px, auto;
    background-position: top 78px center, center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/text-sedona-index.svg"), url("../img/bg-back-desktop@2x.jpg");
      background-size: 460px 350px, 1200px auto;
      background-position: top 78px center, center;
    }

    &::before {
      height: 57px;
    }

    &::after {
      height: 57px;
    }
  }
}
