.page-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;
  margin: 0 auto;
  padding-top: 36px;
  padding-bottom: 27px;
  background-color: #ffffff;
}

.page-footer__hashtag {
  display: block;
  margin-bottom: 36px;
  font-size: 21px;
  font-weight: bold;
  line-height: 26px;
}

@media (min-width: @tablet-width) {
  .page-footer {
    width: 768px;
    flex-direction: row;
    align-items: stretch;
    padding: 36px 60px;
    box-sizing: border-box;
  }

  .page-footer__hashtag {
    margin-top: 10px;
    margin-right: 113px;
    margin-bottom: 0;
  }
}

@media (min-width: @desktop-width) {
  .page-footer {
    width: 1200px;
    padding: 36px 120px;
  }

  .page-footer__hashtag {
    margin-right: 269px;
  }
}
