.popup-success {
  position: fixed;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%);
  width: 320px;
  padding-top: 43px;
  background-color: #ffffff;
  box-shadow: 0 6px 12px -2px rgba(0, 0, 0, 0.2);
  z-index: 1;

  &--hidden {
    display: none;
  }
}

.popup-success__header {
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  color: #000000;
  text-align: center;
}

.popup-success__description {
  padding: 0 35px;
  margin: 0;
  margin-bottom: 35px;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #333333;
  text-align: center;
}

.popup-success__button-wrapper {
  padding: 20px;
  background-color: #f2f2f2;
  box-sizing: border-box;
}

.popup-success__button {
  width: 100%;
  padding: 16px 0;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  border: none;
}

@media (min-width: @tablet-width) {
  .popup-success {
    width: 640px;
    padding-top: 63px;
  }

  .popup-success__header {
    font-size: 30px;
  }

  .popup-success__description {
    padding: 0 99px;
  }

  .popup-success__button-wrapper {
    padding: 44px 91px;
  }

  .popup-success__button {
    font-size: 21px;
  }
}
