.social__list {
  display: flex;
  padding: 0;
  margin: 0;
  margin-bottom: 43px;
  list-style: none;
}

.social__item {
  margin-right: 6px;
  background-color: #81b3d2;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;

    & .social__logo {
      fill: rgba(255, 255, 255, 0.3);
    }
  }
}

.social__link {
  position: relative;
  display: block;
  width: 46px;
  height: 48px;

  &--facebook {
    width: 47px;
  }
}

.social__logo {
  position: absolute;
  fill: #ffffff;

  &--twitter {
    top: 16px;
    left: 12px;
    width: 22px;
    height: 15px;
  }

  &--facebook {
    top: 14px;
    left: 18px;
    width: 11px;
    height: 18px;
  }

  &--youtube {
    top: 17px;
    left: 13px;
    width: 20px;
    height: 14px;
  }
}

@media (min-width: @tablet-width) {
  .social {
    margin-right: 120px;
  }

  .social__list {
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .social {
    margin-right: 275px;
  }
}
