.choise {
  width: 320px;
  margin: 0 auto;
  padding: 28px 20px 49px;
  text-align: center;
  box-sizing: border-box;
  background-color: #ffffff;
}

.choise__title {
  padding: 0;
  margin: 0;
  margin-bottom: 19px;
  font-size: 21px;
  font-weight: bold;
  line-height: 26px;
}

.choice__intro {
  padding: 0;
  margin: 0;
  margin-bottom: 35px;
  font-size: 14px;
  font-weight: normal;
  line-height: 24px;
  color: #333333;
}

.choise__button {
  padding: 16px 0;
  font-size: 21px;
  font-weight: bold;
  line-height: 26px;
}

@media (min-width: @tablet-width) {
  .choise {
    width: 768px;
    padding-top: 56px;
    padding-bottom: 58px;
  }

  .choise__title {
    font-size: 30px;
    margin-bottom: 30px;
  }

  .choice__intro {
    padding: 0 140px;
    margin-bottom: 42px;
  }

  .choise__button {
    width: 458px;
    margin: 0 auto;
  }
}

@media (min-width: @desktop-width) {
  .choise {
    width: 1200px;
    padding-bottom: 66px;
  }

  .choice__intro {
    padding: 0 340px;
    margin-bottom: 47px;
  }
}
